.product-page-row {
    margin: 3rem 1rem;
    display: flex;
    column-gap: 2rem;
    background-color: white;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
}

.product-page-col {
    flex-basis: 50%;
    color: #6c757d;
}

.product-page-img {
    position: relative;
    height: 39rem;
    width: 100%;
}

.product-page-img img {
    height: 100%;
}

.product-page-review {
    display: flex;
    column-gap: 0.5rem;
    font-size: 14px;
}

.product-page-review i {
    color: gold;
}

.product-page-col .smll {
    cursor: pointer;
    font-size: 14px;
}

.product-page-col span {
    color: #6c757d;
    margin: 10px;
}

.product-page-col b {
    color: black;
}

.product-page-col small {
    color: #6c757d;
    margin: 0px 10px;
    cursor: pointer;
    font-size: 12px;
}

.size-flex {
    display: flex;
    column-gap: 10px;
}

.size-flex p {
    background-color: #eee;
    border: 1px solid rgba(0, 0, 0, 0.1);
    width: 1rem;
    text-align: center;
    padding: 5px;
    cursor: pointer;
}

.color-flex {
    display: flex;
    column-gap: 0.3rem;
}

.color-flex .color {
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    padding: 5px;
}

.color-flex .color:first-child {
    background-color: lightcoral;
}

.color-flex .color:nth-child(2) {
    background-color: lightblue;
}

.color-flex .color:last-child {
    background-color: lightgreen;
}

.quantity-lg-flex {
    display: flex;
    column-gap: 2rem;
}

.quantity-flex {
    display: flex;
    border: 1px solid rgba(0, 0, 0, 0.1);
}

.quantity-flex i {
    background-color: #eee;
    display: flex;
    align-items: center;
    width: 2rem;
    justify-content: center;
    cursor: pointer;
}

.quantity-flex input {
    width: 2rem;
    text-align: center;
    border: 0;
}


.add-btn {
    background-color: #232F3E;
    color: white;
    border-radius: 20px;
    padding: 10px;
    border: 0;
    cursor: pointer;
}

.actions-flex {
    display: flex;
    column-gap: 1rem;
}

.actions-flex p {
    cursor: pointer;
}

.info-list {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.info-heading {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0rem 1rem;
    cursor: pointer;
}

.info-text {
    padding: 0rem 1.4rem;
    height: 0rem;
    overflow: hidden;
    transition: 0.5s;
}

.info-text.active {
    height: 4rem;
}

.pay-flex {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.pay-flex img {
    width: 55%;
    height: 3rem;
    object-fit: contain;
}

.desc-container {
    margin: 0rem 1rem;
    border-radius: 10px;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
}

.desc-text {
    background-color: white;
    padding: 20px;
}

.desc-container p {
    color: #6c757d;
    line-height: 28px;
}

.review-container {
    margin: 3rem 1rem;
}

.review-wrapper {
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
}

.review-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1rem;
    padding: 10px 15px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.review-wrapper a {
    color: black;
    cursor: pointer;
}

.review-flex i {
    color: gold;
    font-size: 14px;
}

.review-flex {
    line-height: 35px;
}

.review-form {
    padding: 0rem 0.5rem;
    height: 0rem;
    position: relative;
    overflow: hidden;
    transition: 0.5s;
}

.review-form.active {
    height: 26rem;
}

.form-group {
    display: flex;
    flex-direction: column;
    color: #6c757d;
    row-gap: 0.5rem;
}

.form-group label {
    margin-top: 0.5rem;
}

.form-group input {
    height: 2.5rem;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    outline: red;
}

.form-group textarea {
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    outline: none;
    margin-bottom: 1rem;
}

.form-btn {
    background-color: #232F3E;
    color: white;
    border-radius: 20px;
    padding: 13px 2rem;
    border: 1px solid;
    cursor: pointer;
    position: absolute;
    right: 1%;
}

.sml-img-row {
    display: flex;
    flex-wrap: wrap;
}

.sml-img-row img {
    width: 30%;
    border: 1px dotted rgba(0, 0, 0, 0.1);
    cursor: pointer;
}




