.sub-banner-row {
    display: flex;
    margin-top: 6rem;
}

.sub-banner-col {
    position: relative;
    flex-basis: 24%;
    border-radius: 10px;
    overflow: hidden;
}

.sub-banner-col:hover img {
    scale: 1.1;
}

.sub-banner-img {
    position: relative;
    height: 25rem;
}

.sub-banner-img img {
    width: 100%;
    height: 100%;
    transition: all 0.5s;
}

.mySwiperBanner {
    width: 98%;
}

.sub-banner-col p:first-child {
    font-size: 15px;
    text-transform: lowercase;
}

.sub-banner-col p:last-child {
    font-size: 13.5px;
    line-height: 17px;
}

.sub-banner-col h3 {
    font-size: 19px;
    letter-spacing: 1px;
}

.sub-banner-layer {
    position: absolute;
    top: 0;
    z-index: 4;
    width: 100%;
    padding: 1.5rem 2rem;
    line-height: 11px;
}

.sub-banner-layer.first-layer {
    color: white;
}


@media screen and (max-width: 450px) {
    .swiper-container {
        width: 500px;
    }
}

@media screen and (max-width: 750px) {
    .swiper-container {
        width: 500px;
    }
}

@media screen and (max-width: 900px) {
    .swiper-container {
        width: 900px;
    }
}