.special-swiper {
    width: 98%;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
}

.special-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    border-radius: 10px;
    height: calc((100% - 30px) / 2) !important;
}

.special-col {
    display: flex;
    text-align: start;
    border-radius: 10px;
    gap: 1rem;
}

.special-img {
    flex-basis: 60%;
}

.special-row {
    margin-top: 5rem;
}

.special-row h2 {
    padding-left: 1rem;
    letter-spacing: 2px;
}

.special-col p:first-child {
    font-size: 14px;
}

.special-content h3 {
    line-height: 30px;
    font-size: 18px;
    color: rgba(0, 0, 0, 0.835);
}

.special-content {
    line-height: 18px;
}

.special-content .btn {
    border-radius: 50px;
    background-color: #222f3f;
    color: white;
    border: none;
    padding: 14px 18px;
    cursor: pointer;
}

.special-content .btn:hover {
    background-color: #febc68;
}

.special-img {
    position: relative;
    overflow: hidden;
}

.special-img .special-layer {
    position: absolute;
    display: flex;
    flex-direction: column;
    right: -15%;
    transition: all 0.5s;
}

.special-layer i {
    margin: 5px;
    padding: 5px;
    border-radius: 50px;
    transition: all 0.5s;
    cursor: pointer;
}

.special-layer i:hover {
    background-color: #febc68;
}

.special-col:hover .special-layer {
    right: 0;
}

.special-prices {
    display: flex;
    align-items: center;
}

.special-prices .new-price {
    font-size: 18px;
    color: red;
}

.special-prices .old-price {
    text-decoration: line-through;
    font-size: 18px !important;
    padding-right: 0.5rem;
}

.sml-swiper {
    height: 10rem;
    width: 100%;
}


.sml-slide img {
    width: 100%;
    cursor: pointer;
    height: 6rem;
    object-fit: contain;
}
.sml-slide
{
    position: relative;
    height: 100%;
    width: 100%;
}

.main-img {
    position: relative;
    height: 15rem;
}

.special-link {
    width: 90%;
    height: 100%;
    position: absolute;
    left: 0;
}

.special-layer {
    z-index: 2;
}

@media screen and (max-width: 450px) {
    .swiper-container {
        width: 500px;
    }
}

@media screen and (max-width: 750px) {
    .swiper-container {
        width: 500px;
    }

    .sml-swiper {
        height: 10rem;
        width: 100%;
    }


    .sml-slide img {
        width: 50%;
        cursor: pointer;
    }
}

