.mySwiperCollection {
    width: 98%;
}

.collection-col {
    background-color: white;
    border-radius: 10px;
    cursor: pointer;
    overflow: hidden;
}

.collection-img {
    position: relative;
    height: 15rem;
    width: 100%;
}

.collection-img img {
    position: absolute;
    height: 100%;
    width: 100%;
    object-fit: contain;
}

span {
    color: red;
    font-size: 13px;
}

.collection-col h3 {
    font-size: 15px;
    line-height: 25px;
    color: rgba(0, 0, 0, 0.829);
}

.collection-content {
    padding: 10px;
}

.rating i {
    color: gold;
    font-size: 15px;
    margin: 2px;
}

.second-img {
    transition: 5s;
}

.second-img.show {
    display: block;
    transition: 5s;
}

.second-img.hide {
    display: none;
    transition: 5s;
}

.review-section {
    position: absolute;
    z-index: 7;
    top: 0;
    left: 0;
}

.r-wrapper {
    background-color: #fff;
    display: flex;
    width: 100%;
    padding: 20px;
    position: relative;
}

.review-col {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.2);
    display: none;
    height: 100%;
    align-items: center;
    width: 100%;
    justify-content: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.review-col.show {
    display: flex;
}

.review-col div {
    margin: 10px;
    flex-basis: 50%;
}

.review-col .flx {
    display: flex;
    justify-content: space-between;
    width: 60%;
    align-items: center;
}

.flx select {
    width: 8rem;
    height: 30px;
    border: none;
    background-color: #eee;
}

.flx input {
    background-color: #eee;
    border: none;
    width: 7.7rem;
    height: 28px;
}

.review-col button {
    background-color: #232f3e;
    border: none;
    color: white;
    border-radius: 50px;
    padding: 15px;
    width: 100%;
    cursor: pointer;
}

.review-col button:hover {
    background-color: orange;
}

.review-col i {
    position: absolute;
    top: 2%;
    right: 2%;
    font-size: 30px;
    cursor: pointer;
}

.review-col h3 {
    white-space: pre-wrap;
}

.mySwiperReview {
    width: 100%;
}

.review-img {
    width: 30rem;
}

.review-img img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.collection-layer {
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    right: -15%;
    transition: all 0.5s;
}

.collection-slide:hover .collection-layer {
    right: 0;
}

.collection-layer i:hover {
    background-color: #ffbc69;
}

.collection-layer i {
    margin: 10px;
    color: black;
    padding: 5px;
    border-radius: 50%;
    cursor: pointer;
}

.collection-slide {
    overflow: hidden;
}

.product-link {
    position: absolute;
    top: 0;
    z-index: 2;
    height: 100%;
    width: 83%;
}

.title-link {
    color: black;
}

@media screen and (max-width: 450px) {
    .swiper-container {
        width: 500px;
    }
}

@media screen and (max-width: 750px) {
    .fa-eye {
        display: none;
    }
}

@media screen and (max-width: 900px) {
    .swiper-container {
        width: 900px;
    }
}