.blogs-row {
    display: flex;
    column-gap: 2rem;
    margin: 4rem 1rem;
    color: #6c757d;
}

.blogs-page .cart-heading {
    margin: 2rem 0rem;
}

.blogs-col:first-child {
    flex-basis: 20%;
    background-color: white;
    padding: 15px;
    height: 10rem;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
}

.blogs-col:first-child p {
    cursor: pointer;
    font-size: 15px;
}

.blogs-col b,
h3 {
    color: black;
}

.blogs-col:last-child {
    flex-basis: 80%;
}

.blogs-list-row {
    display: flex;
    column-gap: 2rem;
    display: grid;
    row-gap: 2rem;
    grid-template-columns: repeat(auto-fit, minmax(350px, auto));
}

.blogs-list-col {
    overflow: hidden;
    background-color: white;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.2);
}

.blogs-list-content {
    padding: 15px 15px 25px 15px;
    line-height: 30px;
}

.blogs-list-img {
    position: relative;
    height: 15rem;
    width: 100%;
    overflow: hidden;
}

.blogs-list-img img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    transition: 0.5s;
    overflow: hidden;
}

.blogs-list-col:hover .blogs-list-img img {
    scale: 1.1;
}

.blogs-btn {
    color: white;
    background-color: #232F3E;
    padding: 13px 18px;
    border-radius: 20px;
    cursor: pointer;
    font-size: 14px;
}

.blogs-btn:hover {
    border: 0;
    background-color: #FEBD69;
    color: black;
}

@media screen and (max-width: 1000px) {
    .blogs-row {
        display: flex;
        column-gap: 2rem;
        margin: 5rem 1rem;
        color: #6c757d;
    }

    .blogs-col:first-child {
        flex-basis: 20%;
        background-color: white;
        padding: 15px;
        height: 10rem;
        box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.2);
        border-radius: 10px;
        display: none;
    }

    .blogs-col:first-child p {
        cursor: pointer;
        font-size: 15px;
    }

    .blogs-col b,
    h3 {
        color: black;
    }

    .blogs-col:last-child {
        flex-basis: 100%;
    }

    .blogs-list-row {
        margin: 0rem 4rem;
        display: flex;
        column-gap: 2rem;
        display: grid;
        row-gap: 2rem;
        grid-template-columns: repeat(auto-fit, minmax(300px, auto));
    }

    .blogs-list-col {
        background-color: white;
        box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.2);
    }

    .blogs-list-content {
        padding: 15px;
    }

    .blogs-list-img {
        position: relative;
        height: 15rem;
        width: 100%;
    }

    .blogs-list-img img {
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
}


@media screen and (max-width: 800px) {
    .blogs-list-row {
        margin: 0rem 2rem;
        display: flex;
        column-gap: 2rem;
        display: grid;
        row-gap: 2rem;
        grid-template-columns: repeat(auto-fit, minmax(280px, auto));
    }
}

@media screen and (max-width: 650px) {
    .blogs-list-row {
        margin: 0rem 1rem;
        display: flex;
        column-gap: 2rem;
        display: grid;
        row-gap: 2rem;
        grid-template-columns: repeat(auto-fit, minmax(300px, auto));
    }

    .blogs-row {
        display: flex;
        column-gap: 2rem;
        margin: 5rem 0rem;
        color: #6c757d;
    }

    .blogs-col p {
        cursor: pointer;
        font-size: 15px;
    }

    .blogs-col h3 {
        color: black;
    }
}