.minicart-section {
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0;
    z-index: 5;
    display: block;
    z-index: -1;
}

.minicart-section.show {
    display: block;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.3);
}

.minicart-container {
    background-color: white;
    width: 30%;
    position: absolute;
    right: -30%;
    height: 100%;
    transition: 0.5s;
}

.minicart-container.show {
    right: 0;
}

.minicart-col {
    display: flex;
    color: rgba(0, 0, 0, 0.787);
    margin-top: 2rem;
    margin-bottom: 1rem;
    position: relative;
}

.minicart-img {
    position: relative;
    height: 5rem;
    flex-basis: 40%;
}

.minicart-img img {
    height: 100%;
    width: 100%;
    object-fit: contain;
}

.minicart-content {
    flex-basis: 70%;
}

.minicart-content span {
    color: black;
}

.minicart-content span {
    font-weight: bold;
    font-size: 15px;
}

.minicart-content p {
    line-height: 8px;
    font-size: 14px;
}

.minicart-content h3 {
    font-size: 15px;
    color: rgba(0, 0, 0, 0.721);
    line-height: 20px;
}

.minicart-total {
    display: flex;
    justify-content: space-between;
    padding: 0rem 1rem;
    background-color: #eee;
    flex-direction: column;
}

.minicart-total.hide {
    display: none;
}

.total-flex {
    display: flex;
    justify-content: space-between;
}

.buttons {
    display: flex;
    justify-content: center;
    background-color: #eee;
    border-top: 1px solid rgba(0, 0, 0, 0.115);
}

.buttons .cart-btn {
    margin: 20px 5px;
    background-color: #222F3F;
    color: white;
    border: none;
    padding: 15px 35px;
    border-radius: 50px;
    cursor: pointer;
}

.buttons .cart-btn:last-child {
    background-color: #FFBC69;
    color: black;
}

.buttons .cart-btn:last-child:hover {
    background-color: #222F3F;
    color: white;
}

.buttons .crt-btn:hover {
    background-color: #FFBC69;
    color: black;
}

.minicart-col .remove-btn {
    right: 0;
    font-size: 20px;
    margin-top: 1rem;
    margin-right: 1rem;
    cursor: pointer;
    color: rgba(255, 0, 0, 0.611);
    top: 17%;
}

.cart-title {
    display: none;
    justify-content: center;
    margin-top: 3rem;
}

.cart-title.display {
    display: flex;
}

.add-amount {
    display: flex;
}

.add-amount input {
    width: 20px;
    border: none;
    outline: none;
    padding-left: 0.5rem;
}

.add-amount div {
    background-color: #ddd;
    width: 1rem;
    display: flex;
    justify-content: center;
    cursor: pointer;
    font-size: 17px;
}

.minicart-container i {
    position: absolute;
    right: 2%;
    top: 1%;
    font-size: 25px;
}