.blog-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
}

.blog-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.blog-swiper {
    width: 98% !important;
}

.blog-title {
    padding-left: 1rem;
    letter-spacing: 2px;
}

.blog-slide {
    text-align: start;
}

.blog-img {
    position: relative;
    height: 12rem;
    width: 100%;
}

.blog-img img {
    width: 100%;
    height: 100%;
}

.blog-content {
    padding: 0rem 1rem;
}

.blog-content p:first-child {
    font-size: 14px;
}

.blog-content h3 {
    font-size: 18px;
    color: #000000d9;
    text-overflow: ellipsis;
}

.blog-content p:nth-last-child(2) {
    font-size: 15px;
    line-height: 25px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
}

.blog-content .blog-btn {
    background-color: #222e3e;
    color: white;
    border-radius: 50px;
    border: 0;
    padding: 15px 17px;
    cursor: pointer;
}
.blog-btn:hover
{
    background-color: #FEBC68;
    color: black;
}

.blog-section {
    margin-top: 5rem;
}