.newsletter-footer {
    background-color: #222E3E;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    height: 13rem;
    margin-top: 6rem;
}

.newsletter-text {
    display: flex;
    align-items: center;
    color: white;
}

.newsletter-text i {
    font-size: 25px;
    color: white;
    margin-right: 0.5rem;
}

.newsletter-text p {
    font-size: 28px;
}

.newsletter-input {
    display: flex;
    position: relative;
}

.newsletter-input input {
    width: 40rem;
    height: 45px;
    border: none;
    border-radius: 5px;
    padding-left: 1rem;
    outline: none;
}

.newsletter-input .footer-btn {
    background-color: #222E3E;
    color: white;
    padding: 12px 15px;
    position: absolute;
    right: 1%;
    bottom: 5%;
    border-radius: 5px;
}

.footer-btn:hover {
    background-color: #FFBC69;
    color: black;
}


.footer-bottom {
    background-color: #222E3E;
    display: flex;
    padding: 5rem 1rem;
    border-top: 2px solid rgba(255, 255, 255, 0.201);
}

.footer-col {
    flex-basis: 20%;
    margin: 0px 25px;
}

.footer-col a {
    display: block;
    margin: 22px 0px;
    font-size: 15px;
}

.footer-col p {
    color: white;
    font-size: 20px;
    font-weight: 600;
}

.social-icons {
    display: flex;
    color: white;
    justify-content: space-around;
    width: 100%;
}

.social-icons i {
    font-size: 22px;
    background-color: rgba(128, 128, 128, 0.266);
    border-radius: 50%;
    padding: 7px;
    cursor: pointer;
}

.social-icons i:hover {
    color: #FFBC69;
}

.footer-col p:nth-child(2) {
    font-size: 14px;
    font-weight: 100;
    line-height: 25px;
}

.footer-col.display {
    height: auto;
}

.heading {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: start;
    padding: 0rem;
}
.heading p
{
    font-size: 19px !important;
}

.footer-col .chev {
    color: white;
    cursor: pointer;
    display: none;
}




@media screen and (max-width: 990px) {

    .newsletter-footer {
        background-color: #222E3E;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        height: 13rem;
        margin-top: 6rem;
        flex-wrap: wrap;
        flex-direction: column;
    }

    .newsletter-text {
        display: flex;
        align-items: center;
        color: white;
    }

    .newsletter-text i {
        font-size: 25px;
        color: white;
        margin-right: 0.5rem;
    }

    .newsletter-text p {
        font-size: 28px;
    }

    .newsletter-input {
        display: flex;
        position: relative;
    }

    .newsletter-input input {
        width: 30rem;
        height: 45px;
        border: none;
        border-radius: 5px;
        padding-left: 1rem;
        outline: none;
    }

    .newsletter-input .footer-btn {
        background-color: #222E3E;
        color: white;
        padding: 12px 15px;
        position: absolute;
        right: 0.5%;
        bottom: 3%;
        border-radius: 5px;
    }

    .footer-btn:hover {
        background-color: #FFBC69;
        color: black;
    }


    .footer-bottom {
        background-color: #222E3E;
        display: flex;
        display: grid;
        padding: 5rem 1rem;
        border-top: 2px solid rgba(255, 255, 255, 0.201);
    }

    .footer-col {
        flex-basis: 20%;
        margin: 1rem !important;
        height: 3rem;
        overflow: hidden;
        position: relative;
        background-color: rgba(0, 0, 0, 0.152);
        padding: 10px;
        cursor: pointer;
    }

    .footer-col.display {
        height: auto;
    }

    .heading {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        justify-content: space-between;
        padding: 0rem 1rem 0rem 0.5rem;
    }

    .footer-col .chev {
        color: white;
        cursor: pointer;
    }

    .footer-col a {
        display: block;
        margin: 22px 0px;
        font-size: 15px;
    }

    .footer-col p {
        color: white;
        font-size: 16px !important;
        font-weight: bold !important;
    }

    .social-icons {
        display: flex;
        color: white;
        justify-content: space-around;
        width: 100%;
        display: none;
    }

    .social-icons i {
        font-size: 22px;
        background-color: rgba(128, 128, 128, 0.266);
        border-radius: 50%;
        padding: 7px;
        cursor: pointer;
    }

    .social-icons i:hover {
        color: #FFBC69;
    }

    .footer-col p:nth-child(2) {
        font-size: 14px;
        font-weight: 100;
        line-height: 25px;
    }

    .app-flex img {
        margin: 10px;
    }
}


