.mySwiper2 {
    width: 98%;
    height: 6rem;
}

.brands-col {
    display: flex;
    align-items: center;
}

.brands-col i {
    font-size: 35px;
    margin-right: 1rem;
}

.brands-col h4 {
    font-size: 15px;
}

.brands-col p {
    font-size: 14px;
}

.brands-section {
    padding: 2rem 2rem;
}

.swiper-container {
    width: 480px;
}

@media screen and (max-width: 450px) {
    .swiper-container {
        width: 500px;
    }
}

@media screen and (max-width: 650px) {
    .brands-col {
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

@media screen and (max-width: 900px) {
    .brands-col {
        display: flex;
        align-items: center;
    }
}