.swiper {
    width: 50%;
    height: 100%;
    border-radius: 10px;
}

.mySwiper {
    height: 29rem;
    position: relative;
    top: 0.7rem;
    border-radius: 10px;
}

.swiper-slide {
    font-size: 18px;
    width: 20rem;
    height: 27.7rem;
    border-radius: 10px;
}

.swiper-col:first-child {
    width: 98%;
    height: 100%;
    background-image: url(../images/banner1.webp);
    background-position: center;
    background-size: cover;
    border-radius: 10px;
    position: relative;
}

.swiper-text {
    padding-left: 2rem;
    padding-top: 2rem;
}

.swiper-text p {
    font-size: 14px;
}

.swiper-text h3 {
    font-size: 35px;
}

.swiper-text button {
    background-color: #232f3e;
    color: white;
    border-radius: 50px;
    border: 1px solid;
    padding: 12px;
    cursor: pointer;
}

.swiper-text button:hover {
    background-color: orangered;
}

.swiper-col2 {
    width: 100%;
    height: 100%;
    background-image: url(../images/banner2.webp);
    background-position: center;
    background-size: cover;
    border-radius: 10px;
}

.hero-row {
    display: flex;
    margin-left: 1rem;
    margin-top: 2rem;
}

.hero-col {
    flex-basis: 50%;
}

.sml-hero-row {
    display: flex;
    flex-wrap: wrap;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, auto));
}

.sml-hero-col {
    flex-basis: 43.9%;
    margin: 10px;
    height: 12rem;
    border-radius: 10px;
    padding: 10px;
    display: flex;
    align-items: center;
}

.sml-hero-col.one {
    background-image: url(../images/pcbanner.webp);
    background-position: center;
    background-size: cover;
}

.sml-hero-col.two {
    background-image: url(../images/tabletbanner.webp);
    background-position: center;
    background-size: cover;
}

.sml-hero-col.three {
    background-image: url(../images/watchbanner.webp);
    background-position: center;
    background-size: cover;
}

.sml-hero-col.four {
    background-image: url(../images/headphonebanner.webp);
    background-position: center;
    background-size: cover;
}

.sml-hero-col p:first-child {
    font-size: 13px;
    color: rgba(255, 0, 0, 0.809);
}

.sml-hero-col p:last-child {
    font-size: 13px;
    line-height: 20px;
}




