.filter-slide {
    font-size: 18px;
    background: #fff;
    overflow: hidden;
}

.swiper-filter-img {
    position: relative;
    height: 15rem;
    width: 100%;
}

.swiper-filter-img img {
    height: 100%;
    width: 100%;
    object-fit: contain;
    position: absolute;
}

.swiper-filter-img .second-img {
    display: none;
}

.swiper-filter-img .second-img.show {
    display: block;
}


.filter-swiper {
    width: 63%;
    position: absolute;
    right: 0;
    top: 0;
}

.cats-filter-row {
    position: relative;
    padding: 0rem 0.8rem;
    margin-right: 1rem;
}

.cats-filter-section {
    margin-top: 5rem;
}

.cats-filter-section h2 {
    letter-spacing: 1.5px;
}

.cat-img {
    position: relative;
    height: 21rem;
}

.cat-img img {
    height: 100%;
    position: absolute;
    border-radius: 10px;
}

.swiper-filter-content {
    padding: 0rem 2rem;
    line-height: 24px;
}

.swiper-filter-content p:first-child {
    color: red;
    font-size: 14px;
}

.swiper-filter-content h2 {
    font-size: 15px;
    line-height: 25px;
    color: rgba(0, 0, 0, 0.845);
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.swiper-filter-content .fa-star {
    color: gold;
    margin: 3px;
    font-size: 13px;
}

.filter-flex {
    display: flex;
}

.filter-flex {
    height: 28rem;
    flex-basis: 20%;
}

.cats-list {
    background-color: white;
    flex-basis: 80%;
    line-height: 50px;
    padding: 0rem 1rem;
    border-radius: 10px;
}

.cats-list li {
    font-size: 17px;
}

.filter-layer {
    position: absolute;
    display: flex;
    flex-direction: column;
    right: -20%;
    transition: 0.5s;
}

.filter-slide:hover .filter-layer {
    right: 0;
}

.filter-layer i {
    margin: 7px;
    padding: 5px;
    border-radius: 50px;
    transition: 0.5s;
    cursor: pointer;
}

.filter-layer i:hover {
    background-color: #FFBC69;
}

.modal-section {
    position: absolute;
}

.modal-container {
    background-color: black;
}

.modal {
    display: none;
    background-color: rgba(0, 0, 0, 0.041);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    justify-content: center;
    width: 100%;
    height: 100%;
    align-items: center;
    z-index: 2;
}

.modal.show {
    display: flex;
}

.modal-col {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    width: 48rem;
    height: 26rem;
    position: relative;
}

.modal-flex {
    display: flex;
    align-items: center;
    width: 13rem;
    justify-content: space-between;
}

.modal-flex select {
    width: 8rem;
    height: 34px;
    background-color: #ddd;
    border: none;
}

.modal-flex input {
    height: 30px;
    width: 7.5rem;
    background-color: #ddd;
    border: none;
}

.modal-col .btn {
    background-color: #222e3e;
    color: white;
    border-radius: 50px;
    border: none;
    width: 100%;
    padding: 17px 0px;
    cursor: pointer;
}

.modal-col .btn:hover {
    background-color: #FFBC69;
}

.modal-content {
    line-height: 25px;
    flex-basis: 50%;
    padding: 0rem 2rem;
}

.modal-col i {
    font-size: 27px;
    position: absolute;
    top: 2%;
    right: 2%;
    cursor: pointer;
}

.modal-img {
    flex-basis: 50%;
    position: relative;
    height: 20rem;
    width: 100%;
}

.modal-img img {
    height: 100%;
    width: 100%;
    object-fit: contain;
}

@media screen and (max-width: 500px) {
    .swiper-container {
        width: 500px;
    }
}

