.shop-section .cart-heading {
    margin: 2rem 0rem;
}

.shop-row {
    display: flex;
    margin: 0rem 1rem;
    column-gap: 1rem;
    margin-top: 2rem;
}

.shop-col:first-child {
    flex-basis: 20%;
    color: #6c757d;
}

.shop-col:first-child b {
    color: black;
}

.shop-col:last-child {
    flex-basis: 80%;
}

.filter-1 {
    background-color: white;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
    padding: 10px;
    margin-bottom: 1rem;
    border-radius: 10px;
}

.filter-1 p {
    cursor: pointer;
}

.filter-2 {
    background-color: white;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
    padding: 10px;
    border-radius: 10px;
}

.filter-2 input {
    margin: 12px 0px;
}

.filter-3 {
    display: flex;
    align-items: center;
    column-gap: 0.2rem;
}

.filter-3 input {
    width: 3rem;
    height: 20px;
}

.filter-4 {
    background-color: white;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
    padding: 10px;
}

.filter-4 .color-flex {
    display: flex;
    flex-wrap: wrap;
    column-gap: 0.5rem;
    row-gap: 1rem;
}

.filter-4 .color {
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
    cursor: pointer;
}

.filter-size {
    background-color: white;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
    margin-top: 1rem;
    padding: 10px;
}

.filter-size input {
    margin: 10px 0px;
}


.filter-5 {
    background-color: white;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
    padding: 10px;
}

.filter-5 .tag-flex {
    display: flex;
    flex-wrap: wrap;
}

.tag-flex p {
    background-color: rgb(238, 238, 238, 0.5);
    margin: 10px;
    cursor: pointer;
    padding: 10px;
}

.filter-last {
    background-color: white;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
    padding: 10px;
}

.filter-last-row {
    margin-top: 1.5rem;
}

.filter-last-col {
    display: flex;
    column-gap: 0.5rem;
}

.filter-last-col p {
    font-size: 15px;
    cursor: pointer;
    color: black;
    font-weight: bold;
}

.filter-last i {
    color: gold;
}

.filter-last-img {
    position: relative;
    height: 5rem;
}

.filter-last-img img {
    height: 100%;
    cursor: pointer;
}

.shop-heading {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
    border-radius: 10px;
    margin-bottom: 1rem;
    padding: 0rem 1rem;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
}

.shop-sort {
    display: flex;
    align-items: center;
    column-gap: 1rem;
}

.shop-sort select {
    height: 2rem;
    border: 1px solid rgba(0, 0, 0, 0.2);
}

.shop-layout {
    display: flex;
    align-items: center;
    column-gap: 1rem;
}

.layout {
    background-color: #eee;
    width: 2rem;
    text-align: center;
    margin: 0.2rem 0rem;
    border-radius: 10px;
    cursor: pointer;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.layout.active {
    background-color: #222E3E;
    color: white;
}


.shop-products-row {
    display: flex;
    flex-wrap: wrap;
    column-gap: 0.5rem;
    row-gap: 0.5rem;
}


.shop-products-col {
    flex-basis: 24%;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 5px 6px 0px rgba(0, 0, 0, 0.1);
}

.shop-products-col.col-3 {
    flex-basis: 32.8%;
}

.shop-products-col.col-2 {
    flex-basis: 49.6%;
}

.shop-products-col.col-row {
    flex-basis: 100%;
    display: flex;
}

.shop-products-col.col-row .shop-products-img {
    flex-basis: 100%;
    height: 20rem;
}

.hidden-p {
    display: flex;
}

.hidden-p.show {
    display: flex;
}

.shop-products-content {
    padding: 10px;
}

.shop-products-img {
    position: relative;
    height: 10rem;
    width: 100%;
    overflow: hidden;
}

.shop-products-img img {
    height: 100%;
    width: 100%;
    object-fit: contain;
}


.shop-path {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: white;
    margin-top: 2rem;
    border-radius: 10px;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
    padding: 0rem 0.5rem;
}

.shop-pagination {
    display: flex;
    column-gap: 1rem;
}

.shop-pagination div {
    background-color: white;
    border: 1px solid rgba(0, 0, 0, 0.1);
    color: black;
    border-radius: 50%;
    width: 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 2px;
}

.shop-pagination div:hover {
    background-color: #222E3E;
    color: white;
}

.shop-pagination div.active {
    background-color: #222E3E;
    color: white;
}

.shop-products-rating {
    color: gold;
}

.shop-products-layer {
    position: absolute;
    top: 5%;
    display: flex;
    flex-direction: column;
    right: -20%;
    row-gap: 10px;
    transition: 0.5s;
}

.shop-products-col:hover .shop-products-layer {
    right: 3%;
}

.shop-products-layer i {
    font-size: 20px;
    transition: 0.5s;
    cursor: pointer;
    padding: 3px;
    border-radius: 50%;
}

.shop-products-layer i:hover {
    background-color: #FEBD69;
}

.filter-sort {
    display: flex;
    align-items: center;
    column-gap: 1rem;
}

.filter-hidden {
    background-color: #222E3E;
    color: white;
    height: 2rem;
    display: none;
    align-items: center;
    padding: 3px 10px;
    cursor: pointer;
}

.filter-icon {
    display: none;
}

.cart-heading {
    display: flex !important;
    align-items: center;
    column-gap: 1rem;
    justify-content: center;
}

