@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&family=Roboto:ital,wght@0,400;1,300&display=swap');

body {
    margin: 0;
    padding: 0;
    font-family: 'Roboto';
    background-color: #f5f4f6;
}

a {
    text-decoration: none;
    color: white;
}

.top-navbar {
    display: flex;
    justify-content: space-between;
    background-color: #121821;
    color: white;
    height: 2.5rem;
    font-size: 14px;
    padding: 0rem 1rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.177);
    align-items: center;
}

.top-navbar i {
    color: white;
}

.top-navbar-right {
    display: flex;
    width: 29rem;
    justify-content: space-around;
}

.top-navbar-right .hotline {
    border-right: 1px solid;
    padding-right: 10px;
}

.price {
    display: flex;
    align-items: center;
}

.languages {
    display: flex;
    align-items: center;
}

.middle-navbar {
    background-color: #121821;
    color: white;
    display: flex;
    justify-content: space-between;
    height: 5rem;
    align-items: center;
    padding: 0rem 1rem;
}

.middle-navbar-right {
    display: flex;
    align-items: center;
    width: 35rem;
    justify-content: space-around;
    font-size: 14px;
}
.middle-navbar-right i{
    cursor: pointer;
}

.middle-navbar-center {
    position: relative;
}

.middle-navbar-center input {
    width: 27rem;
    height: 37px;
    border-radius: 10px;
    border: 1px solid;
    outline: none;
}

.middle-navbar-center i {
    background-color: orangered;
    padding: 11.5px;
    position: absolute;
    right: 0;
    bottom: 1.2%;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    width: 2rem;
    cursor: pointer;
}

.compare-flex {
    display: flex;
    align-items: center;
}

.wish-flex {
    display: flex;
    align-items: center;
}

.log-flex {
    display: flex;
    align-items: center;
}

.cart-flex {
    display: flex;
    align-items: center;
}

.compare-text,
.log-text,
.wish-text,
.cart-text {
    display: flex;
    flex-direction: column;
    line-height: 22px;
}

.middle-navbar-right i {
    font-size: 35px;
    padding-right: 10px;
}

.middle-navbar-left {
    font-size: 21px;
}

.bottom-navbar {
    display: flex;
    background-color: #232f3e;
    color: white;
    align-items: center;
    padding-left: 1rem;
    font-size: 15px;
    height: 3rem;
}

.bottom-navbar-left {
    padding-right: 6rem;
    display: flex;
    align-items: center;
}

.bottom-navbar-left i {
    font-size: 30px;
}

.bottom-navbar a {
    margin: 10px;
}

.home-link {
    display: flex;
    align-items: center;
}

.bottom-navbar-center {
    display: flex;
    align-items: center;
}

li {
    list-style: none;
    cursor: pointer;
}

.dropdown {
    position: absolute;
    background-color: #121821;
    list-style: none;
    padding: 0;
    margin: 0;
    left: 1%;
    width: 17rem;
    margin-top: 0.6rem;
    transition: all 0.5s;
    z-index: 2;
    height: 0;
    overflow: hidden;
}

.dropdown:hover {
    overflow: visible;
}

.dropdown.show {
    height: 39rem;
}

.dropdown.hide {
    height: 0;
}

.dropdown li {
    line-height: 47px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.143);
}

.dropdown li a {
    font-weight: 100;
    font-size: 14px;
}

.link-flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(255, 255, 255, 0.109);
}

.link-flex i {
    font-size: 20px;
}

.list-dropdown {
    position: absolute;
    display: none;
    width: 40rem;
    background-color: #eee;
    color: black;
    left: 100%;
}

.list-dropdown.show {
    display: flex;
    animation: showDropdown 0.5s linear 1 forwards;
}

@keyframes showDropdown {
    from {
        opacity: 0.5;
        transform: translateY(-10%);
    }

    to {
        opacity: 1;
        transform: translateY(-14%);
    }
}

.list-dropdown-col {
    flex-basis: 33.33%;
    margin: 5px;
}

.list-dropdown-col a {
    display: block;
    margin: 0;
    padding: 0;
    color: black;
    line-height: 30px;
}

.menu-icon {
    display: none;
}

.middle-navbar-left i
{
    display: none;
}


