    @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&family=Roboto:ital,wght@0,400;1,300&display=swap');

    body {
        margin: 0;
        padding: 0;
        font-family: 'Roboto';
    }

    a {
        text-decoration: none;
        color: white;
    }

    .top-navbar {
        display: flex;
        justify-content: space-between;
        color: white;
        height: 2.5rem;
        font-size: 14px;
        padding: 0rem 1rem;
    }

    .top-navbar i {
        color: white;
    }

    .top-navbar-left {
        display: none;
    }

    .top-navbar-right {
        display: flex;
        width: 29rem;
        justify-content: right;
    }

    .top-navbar-right p {
        border: none !important;
    }

    .price {
        display: flex;
        align-items: center;
        display: none;
    }

    .languages {
        display: flex;
        align-items: center;
        display: none;
    }

    .middle-navbar {
        color: white;
        display: flex;
        justify-content: space-between;
        height: 5rem;
        align-items: center;
        padding: 0rem 1rem;
    }

    .middle-navbar-left 
    {
        display: flex;
        align-items: center;
    }
    .middle-navbar-left i
    {
        display: block;
        font-size: 33px;
        margin-right: 1rem;
        cursor: pointer;
    }

    .middle-navbar-right {
        display: flex;
        align-items: center;
        width: 10rem;
        justify-content: space-around;
    }

    .middle-navbar-center {
        position: relative;
        display: none;
    }

    .middle-navbar-center input {
        width: 27rem;
        height: 40px;
        border-radius: 10px;
        border: 1px solid;
    }

    .middle-navbar-center i {
        background-color: orangered;
        padding: 13.3px;
        position: absolute;
        right: 0;
        bottom: 1.2%;
        border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
        width: 2rem;
        cursor: pointer;
    }

    .compare-flex {
        display: flex;
        align-items: center;
    }

    .wish-flex {
        display: flex;
        align-items: center;
    }

    .log-flex {
        display: flex;
        align-items: center;
    }

    .cart-flex {
        display: flex;
        align-items: center;
    }

    .compare-text,
    .log-text,
    .wish-text,
    .cart-text {
        display: flex;
        flex-direction: column;
        line-height: 25px;
        display: none;
    }

    .middle-navbar-right i {
        font-size: 30px;
        padding-right: 10px;
    }

    .middle-navbar-left {
        font-size: 20px;
    }

    .bottom-navbar {
        display: flex;
        background-color: #232f3e;
        color: white;
        align-items: center;
        padding-left: 1rem;
    }

    .bottom-navbar-left {
        padding-right: 4rem;
        display: flex;
        align-items: center;
    }

    .bottom-navbar-left i {
        font-size: 30px;
    }

    .bottom-navbar-menu
    {
        display: none;
    }

    .bottom-navbar-left p
    {
        display: none;
    }

    .bottom-navbar a {
        margin: 10px;
    }

    .home-link {
        display: flex;
        align-items: center;
    }

    .bottom-navbar-center {
        display: flex;
        display: none;
    }

    .bottom-navbar-center a {
        line-height: 35px !important;
    }

    .dropdown {
        position: absolute;
        background-color: #121821;
        list-style: none;
        padding: 0;
        height: auto;
        margin: 0;
        left: -60%;
        width: 17rem;
        margin-top: 0.6rem;
        top: 5%;
        visibility: hidden;
    }

    .dropdown.show 
    {
        left: 0;
        visibility: visible;
    }

    .dropdown li {
        line-height: 60px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.143);
    }

    .dropdown li a {
        font-weight: 100;
        font-size: 14px;
    }

    .link-flex {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: 1px solid rgba(255, 255, 255, 0.109);
    }

    .link-flex i {
        font-size: 20px;
    }

    .menu-icon {
        display: block;
        position: absolute;
        right: 0;
        background-color: rgba(14, 14, 26, 0.669);
    }
}