.contact-page .cart-heading {
    margin: 2rem 0rem;
}

.contact-wrapper {
    background-color: white;
    margin: 0rem 1rem;
    padding: 1rem;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
}

.contact-row {
    display: flex;
    column-gap: 3rem;
}

.contact-col {
    flex-basis: 50%;
    color: #6c757d;
}

.contact-col form {
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
}

.contact-col input {
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: #eee;
    border-radius: 5px;
    height: 2.5rem;
    padding-left: 1rem;
    outline: none;
}

.contact-col textarea {
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: #eee;
    border-radius: 5px;
    padding-left: 1rem;
    outline: none;
    padding-top: 1rem;
}

.contact-btn {
    background-color: #232F3E;
    transition: 0.3s;
    color: white;
    padding: 1rem 0rem;
    border-radius: 5px;
    border: 1px solid;
    cursor: pointer;
}

.contact-btn:hover {
    background-color: #FEBD69;
    color: black;
    border: 1px solid orange;
}

.contact-col:last-child {
    display: flex;
    flex-direction: column;
    position: relative;
    bottom: 4rem;
}

.contact-col:last-child h2 {
    color: black;
}

.contact-map {
    margin: 2rem 0rem;
}

.contact-map iframe {
    width: 99%;
}

