.last-slide {
    text-align: center;
    font-size: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.last-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.last-swiper {
    width: 95%;
    height: 10rem;
    z-index: 5;
    background-color: white;
    margin-top: 5rem;
    padding: 0rem 1rem;
}