.cat-slide {
    font-size: 18px;
    background: #fff;
    height: calc((100% - 30px) / 2) !important;
    display: flex;
    justify-content: center;
    align-items: center;
}

.swiper-cat {
    width: 98%;
    height: 15rem;
    background: #fff;
    padding: 2rem 0rem;
    box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.2);
}

.cat-col {
    display: flex;
    align-items: center;
}

.cat-image {
    position: relative;
    height: 8rem;
}

.cat-image img {
    height: 100%;
    object-fit: contain;
}

.cat-text {
    margin-right: 1rem;
}

.cat-col h2 {
    font-size: 15px;
    color: rgba(0, 0, 0, 0.709);
}

.cat-col p {
    font-size: 14px;
}


@media screen and (max-width: 500px) {
    .swiper-container {
        width: 500px;
    }
}

@media screen and (max-width: 750px) {
    .swiper-container {
        width: 500px;
    }
}

@media screen and (max-width: 900px) {
    .swiper-container {
        width: 950px;
    }
}


@media screen and (max-width: 1000px) {
    .swiper-container {
        width: 900px;
    }
}